function extractResultTableDataFromResponse(response) {
    
    const summary = response.checkSummary;
    const entity = response.entityResult;
    const requestId = response.requestId;
    const recomm = summary.resultNotes && summary.resultNotes
                    .find( el => el["kvpKey"] === "Action.Recommended" );
    const status = recomm && recomm["kvpValue"];

    return {
        requestId: requestId || summary.checkId, //missing, using other ID
        displayName: entity.nameCheck.name.displayName,
        initialRisk: summary.riskLevel || 30, // missing
        matchStrength: summary.confidenceLevel,
        result: summary.resultState,
        status: status || 'Pass', //missing
        get resultLabel() {
            
            const r = this.result
            if (r === 'CHECKED_SUCCESS')
                return 'Success';
            return 'Fail';

            // if ([
            //     'CHECKED_SUCCESS',
            //     'CHECKED_SUCCESS_CLEAR',
            //     'CHECKED_SUCCESS_WITH_NOTES'
            // ].findIndex(e => e === r) != -1) return 'Success';

            // if ([
            //     'UNCHECKED',
            //     'NOT_SUPPORTED',
            //     'CHECKING',
            //     'UNPROCESSABLE',
            //     'NO_MATCH'
            // ].findIndex(e => e === r) != -1) return 'Failed';

            // if ([
            //     'CHECKED_PARTIAL_SUCCESS'
            // ].findIndex(e => e === r) != -1) return 'Partial';

            // return 'NONE';
        },
        get riskLabel() {
            const r = this.initialRisk

            if (r < 35) return 'Low';
            if (r < 60) return 'Medium';
            
            return 'High'
        }
    };
}