export default function isValidDateOfBirth(str) {
    const [ year, month, day ] = str.split('-');
    
    if (!(year > 1900))
        return 'Invalid Year';
    if (month && !(month <= 12 && month >= 1))
        return 'Invalid Month';
    if (day && !(day <= 31 && day >= 1))
        return 'Invalid day';

    if (!(year && month && day))
        return '-';
    return true
}
