<template lang="pug">
    .options-list
        ul
            li( v-for="(opt, i) in options" :key="(opt.label+opt.value)" 
                :class="{selected: value && opt.value === value.value, 'is-empty': !opt.label || opt.value.length === 0 }"
                @click="clicked(opt)" ) {{ opt.label }}

</template>
<style lang="sass" scoped>
@import '~@/styles/config'
@import '~@/styles/scrollbar'

$border-style: 1px solid $COLOR__LIGHTGREY
.options-list
    +style-scrollbar()
    max-height: 400px
    overflow-y: auto
li
    padding: #{$LENGTH__SM_PADDING/2}  #{$LENGTH__SM_PADDING} 
    list-style: none
    width: 100%
    border: $border-style
    border-top: none
    cursor: pointer
    transition: background-color .2s ease-out
    background-color: white
    &:nth-of-type(1)
        border-top: $border-style
    &.is-empty
        background-color: $COLOR__LIGHTGREY
        font-size: 12px
        min-height: 1.4em
        line-height: 1em
    &:hover, &.selected
        background-color: lighten($COLOR__ACCENT, 21%)

</style>

<script>
export default {
    props: {
        // options: [{ label, value }]
        options: Array,
        value: Object
    },
    methods: {
        clicked(data) {
            this.$emit('input', data);
            this.$emit('click');
        }
    }
}
</script>
