<template lang="pug">
    .input.list.pick.clickable( :class="{toggled: isToggled}" )
        label-wrapper( :label="label" :for="id" )
            .input-and-dropdown
                .input-like( @click="toggle" )
                    span.label( v-if="selectedLabel") {{ selectedLabel }}
                    span.placeholder( v-else ) {{ placeholder }}
                    svgicon.toggle( name="arrow-toggle" width="8px" height="8px" )
                dropdown-options-list.collapsible-list( :options="options" :value="value" @input="changed" )
</template>
<style lang="sass" scoped>
@import '~@/styles/config'

.input-like
    position: relative
.toggle
    position: absolute
    right: $LENGTH__SM_PADDING
    top: calc(50% - 4px)
    transition-property: fill, transform
    transition-duration: .5s 
    transition-timing-function: ease-out

.input
    &.toggled .input-like
        border-color: $COLOR__BODY
    .input-like:hover .toggle
        transform: translateY(-2px)
    &.toggled .input-like .toggle
        transform: translateY(-2px) rotateZ(180deg)
    .input-like:hover .toggle, &.toggled .toggle
        fill: $COLOR__ACCENT

</style>

<script>
import '@/components/icons/arrow-toggle';
import InputMixin from './Input.mixin'
import ToggleMixin from './Toggle.mixin'
import DropdownOptionsList from '@/components/inputs/DropdownOptionsList'
import LabelWrapper from '@/components/wrappers/LabelMessageWrapper'

export default {
    mixins: [InputMixin, ToggleMixin],
    components: {
        DropdownOptionsList,
        LabelWrapper
    },
    props: {
        options: Array
    },
    computed: {
        selectedLabel() {
            return this.value && this.value.label
        },
    },
    methods: {
        changed(value) {
            this.$emit('input', value)
            this.toggle()
        }
    }
}
</script>
