<script>
import SmartInputMixin from '@/components/smart-inputs/SmartInput.mixin';
import FullNameInput from '@/components/smart-inputs/FullNameInput';
import NationalityListInput from '@/components/smart-inputs/NationalityListInput';
import DobInput from '@/components/smart-inputs/DateOfBirthInput';
import AddressInput from '@/components/smart-inputs/AddressInput';
import IdTypeInput from '@/components/smart-inputs/IdTypeInput';
import DocumentNumberInput from '@/components/smart-inputs/DocumentNumberInput';

import { formatDOBFromNumbers, extractNumbersFromDOB } from '@/utils/formatters/dateOfBirth';

export default {
    mixins: [SmartInputMixin],
    components: {
        NationalityListInput,
        FullNameInput,
        DobInput,
        AddressInput,
        IdTypeInput,
        DocumentNumberInput
    },
    props: {
        complete: Boolean
    },
    computed: {
        name: {
            get() {
                return this.value.name
            },
            set(name) {
                const valueCopy = this.copyValue();
                valueCopy.name = { ...name }
                this.emitValue(valueCopy);
            }
        },
        nationality: {
            // TRANSLATE INPUT LANGUAGE INTO SEARCH VIEW LANGUAGE
            get() {
                return {
                    label: this.value.nationality.label,
                    value: this.value.nationality.code
                }
            },
            set(nationality) {                
                const valueCopy = this.copyValue()
                valueCopy.nationality = {
                    code: nationality.value,
                    label: nationality.label
                };
                this.emitValue(valueCopy);
            }
        },
        dob: {
            get() {
                return extractNumbersFromDOB(this.value.dob);
            },
            set(dobNumbers) {
                const valueCopy = this.copyValue();
                valueCopy.dob =  formatDOBFromNumbers(dobNumbers);
                this.emitValue(valueCopy);
            }
        },
        address: {
            get() {
                return this.value.address
            },
            set(add) {
                const valueCopy = this.copyValue();
                valueCopy.address = add;
                this.emitValue(valueCopy);
            }
        },
        idType: {
            get() {
                return this.value.id.type
            },
            set(type) {
                const valueCopy = this.copyValue();
                valueCopy.id.type = type;
                this.emitValue(valueCopy);
            }
        },
        docNumber: {
            get() {
                return this.value.id.number
            },
            set(numb) {
                const valueCopy = this.copyValue();
                valueCopy.id.number = numb;
                this.emitValue(valueCopy)
            }
        }
    },
}
</script>
<template lang="pug">
    .name-details-form
        h3 Name and Details
        .inputs.basic
            full-name-input#full-name( label="Name" placeholder="Name" v-model="name")
            nationality-list-input#nationality( label="Nationality (optional)" placeholder="Nationality" v-model="nationality" )
            dob-input#date-of-birth( label="Date of Birth" placeholder="YYYY-MM-DD" v-model="dob" )
        transition( name="fade-slide-right" )
            .inputs.complete( v-if="complete" )
                address-input#address( label="Home Address (optional)" placeholder="Address"  v-model="address" )
                id-type-input#id-type( label="ID Type (optional)" placeholder="Document" v-model="idType")
                document-number-input#doc-number( :disabled="!idType || idType.length === 0" label="Document Number" placeholder="Number" v-model="docNumber" )


</template>
<style lang="sass" scoped>
@import '~@/styles/transitions'
@import './search-form-styles'

</style>
