<template lang="pug">
    .id-type-input.input
        svgicon.return-toggle( v-if="isMedicare" name="arrow-toggle" width="16px" @click="erase" )
        list-pick-input( v-if="!isMedicare" v-bind="attrs" :options="options" v-model="type" )
        list-pick-input( v-else ref="medInput"
            label="ID Type / Medicare Card" 
            placeholder="Medicare Card"
            v-bind="attrs" 
            v-model="medType"
            :options="medOptions" )

</template>
<style lang="sass" scoped>
@import '~@/styles/config'
.id-type-input.input
    overflow: visible
.id-type-input
    position: relative
.return-toggle
    transform: rotateZ(90deg)
    position: absolute
    left: -24px
    top: 4px
    fill: $COLOR__TEXT_GREY
    cursor: pointer
    &:hover
        fill: $COLOR__ACCENT
</style>

<script>
import SmartInputMixin from './SmartInput.mixin'
import ListPickInput from '@/components/inputs/ListPickInput'
export default {
    mixins: [SmartInputMixin],
    components: {
        ListPickInput
    },
    
    computed: {
        isMedicare() {
            const isMedicareType = !!(this.type && this.isMedicareValue(this.type.value))

            const medTypeValues = this.medOptions.map(el=>el.value);
            const isCardType = !!(this.medType && medTypeValues.findIndex(v=>v===this.medType.value) != -1)

            return isMedicareType || isCardType
        },
        type: {
            get() {
                return this.options.find( el => el.value === this.value )
            },
            set(type) {
                this.emitValue(type.value);
                
                this.$nextTick(()=>{
                    if (this.$refs.medInput)
                        this.$refs.medInput.toggle()
                });
            }
        },
        medType: {
            get() {
                return this.medOptions.find( el => el.value === this.value )
            },
            set(type) {
                this.emitValue(type.value)
            }
        },
        medOptions() {
            return [
                { label: 'Green', value: 'G' },
                { label: 'Blue', value: 'B' },
                { label: 'Yellow', value: 'Y' }
            ]
        },
        options() {
            return [
                { label: "", value: "" },
                { label: "Drivers License", value: "DRIVERS_LICENCE" },
                { label: "Passport", value: "PASSPORT" },
                { label: "Medicare Card", value: "NATIONAL_HEALTH_ID" }
            ]
        }
    },
    methods: {
        erase() {
            this.emitValue(null)
        },
        isMedicareValue(value) {
            return value === 'NATIONAL_HEALTH_ID'
        }
    }
}
</script>
