<script>
import NameDetailsForm from '@/components/module/search/NameAndDetailsSearchForm';
import FuzzinessForm from '@/components/module/search/FuzzinessForm';
import ButtonInput from '@/components/inputs/ButtonInput';
import ToggleInput from '@/components/inputs/ToggleInput';
import ResultTable from '@/components/module/search/ResultTable';

import isValidDateOfBirth from '@/utils/validators/dateOfBirth'

import api from '@/api'
const isEmpty = str => !str || str.length === 0
const isValidDob = dob => isValidDateOfBirth(dob) === true
export default {
    constants: {
        KYC_SWITCH: 1,
        AML_SWITCH: 2
    },
    components: {
        ToggleInput,
        NameDetailsForm,
        ButtonInput,
        FuzzinessForm,
        ResultTable
    },
    data() {
        return {
            nameAndDetails: {
                name: {
                    familyName: '',
                    middleName: '',
                    givenName: ''
                },
                nationality: {
                    code: '',
                    label: null
                },
                dob: '',
                address: '',
                id: {
                    type: '',
                    number: ''
                }
            },
            fuzzinessLevel: null,
            switchConfig: this.KYC_SWITCH,
            searchResults: []
        }
    },
    computed: {
        canSubmit() {
            const { name, dob } = this.nameAndDetails;
            
            return !isEmpty(name.givenName) && !isEmpty(name.familyName) && isValidDob(dob)
        },
        kycSwitchState: {
            get () {
                const c = this.switchConfig
                const k = this.KYC_SWITCH
                const join = c & k
                
                return Boolean(join === k)
            },
            set (state) {
                if (state === false) {
                    // for KYC to be turned off, AML must be set on
                    // remove KYC from config and then add AML
                    // removing 
                    const removed = (this.KYC_SWITCH ^ this.switchConfig)
                    // adding and setting
                    this.switchConfig = removed | this.AML_SWITCH
                } else 
                    this.switchConfig = this.switchConfig | this.KYC_SWITCH
            }
        },
        amlSwitchState: {
            get () {
                const c = this.switchConfig
                const k = this.AML_SWITCH
                const join = c & k
                
                return Boolean(join === k)
            },
            set (state) {
                if (state === false) {
                    // for KYC to be turned off, AML must be set on
                    // remove KYC from config and then add AML
                    // removing 
                    const removed = (this.AML_SWITCH ^ this.switchConfig)
                    // adding and setting
                    this.switchConfig = removed | this.KYC_SWITCH
                } else 
                    this.switchConfig = this.switchConfig | this.AML_SWITCH
            }
        },
        gotResults() {
            return this.searchResults && this.searchResults.length > 0
        }
    },
    methods: {
        async fetchSearchResults() {
            if (!this.canSubmit)
                return;

            const details = this.nameAndDetails;

            const entity = {
                addresses: [{
                    country: details.nationality,
                    longForm: details.address
                }],
                dateOfBirth: {
                    dateOfBirth: details.dob
                },
                indentityDocs: [{
                    country: details.nationality.code,
                    idNumber: details.id.number,
                    idType: details.id.type,
                    region: details.nationality
                }],
                name: {
                    familyName: details.name.familyName,
                    middleName: details.name.middleName,
                    givenName: details.name.givenName
                }
            }
            
            this.searchResults = [await api.fetchSummary(entity)];
        }
    }
}
</script>

<template lang="pug">
    .search-view( :class="[!!kycSwitchState? 'complete-kyc' : 'partial-kyc', {  fuzzy: amlSwitchState } ]" )

        .switches
            toggle-input( label="KYC" v-model="kycSwitchState" )
            toggle-input( label="AML" v-model="amlSwitchState" )
        form.form-area( autocomplete="off" @submit.prevent )
            .forms
                name-details-form( v-model="nameAndDetails" :complete="!!kycSwitchState" )
                transition( name="fade-slide-right" )
                    fuzziness-form( v-if="amlSwitchState" v-model="fuzzinessLevel" )
            .search-button-container
                button-input#submit-button( type="button" @click="fetchSearchResults" :disabled="!canSubmit" ) Search Now
        .results-area
            transition( name="fade-in" )    
                result-table.result-table( v-if="gotResults" :resultList="searchResults" )
</template>
<style lang="sass" scoped>
@import '~@/styles/config'
@import '~@/styles/transitions'

.search-view
    
    &.complete-kyc 
        .form-area
            padding-bottom: 0
        .search-button-container
            padding-bottom: 48px
    &.fuzzy 
        .form-area
            padding-bottom: 48px
        .search-button-container
            padding-bottom: 0px

.switches
    padding-top: 28px
    text-align: right
    padding-right: 30px
    .toggle
        display: inline-block
        &:nth-of-type(1)
            margin-right: 40px
.form-area
    display: flex
    align-items: stretch
    border-bottom: 1px solid $COLOR__GREY
    .forms
        flex: 1 1 auto
    .search-button-container
        width: 292px
        flex: 0 0 auto
        display: flex
        flex-direction: column
        justify-content: flex-end
