<template lang="pug">
    text-input( v-bind="attrs" v-model="number" )
</template>
<script>
import SmartInputMixin from '@/components/smart-inputs/SmartInput.mixin'
import TextInput from '@/components/inputs/TextInput'
export default {
    mixins: [SmartInputMixin],
    components: {
        TextInput
    },
    computed: {
        number: {
            set(numb) {
                this.emitValue(numb)
            }, 
            get() {
                return this.value
            }
        }
    }
}
</script>
