<template lang="pug">
    text-input( v-bind="attrs" v-model="dob" :validation="validationState" )
</template>
<script>
import SmartInputMixin from '@/components/smart-inputs/SmartInput.mixin'
import TextInput from '@/components/inputs/TextInput'
import { formatDOBFromNumbers, extractNumbersFromDOB } from '@/utils/formatters/dateOfBirth';
import isValidDateOfBirth from '@/utils/validators/dateOfBirth';


export default {
    mixins: [SmartInputMixin],
    components: {
        TextInput
    },
    computed: {
        dob: {
            get() {
                const dob = this.formatDOBFromNumbers(this.value);
                if (dob.length >= 4) {
                    let validation = isValidDateOfBirth(dob);
                    if (validation === '-')
                        validation = true;
                    this.validationState = validation;
                }
                
                return dob
            },
            set(dob) {
                const numbers = this.extractNumbersFromDOB(dob);
                this.emitValue(numbers);
            }
        },
    },
    methods: {
        formatDOBFromNumbers,
        extractNumbersFromDOB,
    }
}
</script>
