<template lang="pug">
    .label-wrapper
        .label-container
            label( v-bind="attrs" ) {{ label }}
        .content-container
            slot
        p.message-container( v-if="$slots.message" )
            slot( name="message" )
</template>
<style lang="sass" scoped>
$space: 12px
.label-container
    padding-bottom: $space
.message-container
    padding: $space
    padding-top: 4px
</style>

<script>
export default {
    inheritAttrs: false,
    props: {
        label: String,
    },
    computed: {
        attrs() {
            const { label, ...attrs } = this.$attrs;
            return attrs;
        }
    }
}
</script>
