export function formatDOBFromNumbers(numbers) {
    let   pattern = '####';
    if (numbers.length > 4)
        pattern += "-##";
    if (numbers.length > 6)
        pattern += "-##";

    let i = 0;

    const dob = pattern.replace(/#/g, _=>numbers.charAt(i++));

    return dob
    
}
export function extractNumbersFromDOB(dob) {
    
    return dob.replace(/\D/g, '');
}