        .table
            .columns
                .field-title.column( v-for="{label, field} in columns" :key="field" :class="[field]" )
                    h5.label {{ label }}
                    svgicon.order-toggle( name="arrow-toggle" width="8px" )
            
            transition-group.rows( tag="div" name="rows" )
                .row( v-for="result in resultList" :key="result.requestId" 
                    :class="{ selected: selectedId === result.requestId }"
                    @click="select(result.requestId)" )
                    .field.column( v-for="{label, field} in columns" :key="field" 
                        :class="[field, field === 'status' ? result[field] : '' ]" )
                        progress-bar( v-if="field === 'matchStrength'" :progress="result[field]/100" )
                        span( v-else ) {{ result[field] }}

