<script>
import ProgressBar from '@/components/inputs/ProgressBar'
export default {
    components: {
        ProgressBar
    },
    constants: {
        columns: [
            {label: 'RequestID', field: 'requestId'},
            {label: 'Display Name', field: 'displayName'},
            {label: 'Initial Risk', field: 'riskLabel'},
            {label: 'Match Strength', field: 'matchStrength'},
            {label: 'Result', field: 'resultLabel'},
            {label: 'Status', field: 'status'}
        ]
    },
    props: {
        resultList: Array
    },
    data: () => ({
        selectedId: null
    }),
    methods: {
        select(id) {
            if (id === this.selectedId)
                this.selectedId = null
            else 
                this.selectedId = id
            
        }
    }
}
