<template lang="pug">
    text-input( v-bind="attrs" v-model="address" :validation="validationState" @blur="verifyAddress(address)")
</template>
<script>
import SmartInputMixin from '@/components/smart-inputs/SmartInput.mixin'
import TextInput from '@/components/inputs/TextInput'

import db from '@/utils/debounce';
import { isAddressValid, isAddressVerified } from '@/utils/validators/address';

export default {
    mixins: [SmartInputMixin],
    components: {
        TextInput
    },
    constants: {
        invalidAddressMessage: "Current address is invalid",
        fakeAddressMessage: "Current address is incomplete or doesn't exist"
    },
    computed: {
        address: {
            get() {
                return this.value;
            },
            set(add) {
                if (add.length < 2)
                    // so typing from scratch resets validation
                    this.validationState = true
                else {
                    this.testAddress(add)
                } 
                this.emitValue(add);
                
            }
        }
    },
    methods: {
        verifyAddress: db(async function(address) {
            if (!address || address.length === 0)
                return;
            const exists = await isAddressVerified(address);
            const state = exists || this.fakeAddressMessage;
            this.validationState = state;
        }, 750),
        testAddress: db(async function (address) {
            const isValid = await isAddressValid(address);            
            const state = isValid || this.invalidAddressMessage;
            this.validationState = state;
        }, 750)
    }
}
</script>
