<script>
import SmartInputMixin from '@/components/smart-inputs/SmartInput.mixin'
import FuzzinessIntervalInput from '@/components/smart-inputs/FuzzinessIntervalInput'

export default {
    mixins: [SmartInputMixin],
    components: {
        FuzzinessIntervalInput
    },
    computed: {
        fuzzinessLevel: {
            get() {
                return this.value
            },
            set(lvl) {
                this.emitValue(lvl)
            }
        }
    }
}
</script>

<template lang="pug">
.fuzziness-form
    h3 Fuzziness
    .inputs
        fuzziness-interval-input.no-message( label="Fuzziness Interval" placeholder="Select" v-model="fuzzinessLevel" )
        p.help
            | Level of flexibility of search (0% will check for exact matches only;
            br
            | 100% will look for results with even the slightest match in spelling and phonetics)
</template>
<style lang="sass" scoped>
@import './search-form-styles'
@import '~@/styles/config'
.inputs
    align-items: flex-end
