<template lang="pug">
    filter-list-pick-input( 
        v-bind="attrs" 
        :value="value" @input="emitValue"
        :options="nationalityOptions" 
        :filter.sync="filterText" )  
</template>
<style lang="sass" scoped>

</style>

<script>
import SmartInputMixin from '@/components/smart-inputs/SmartInput.mixin';
import FilterListPickInput from '@/components/inputs/FilterListPickInput';
import av from '@/api/AddressValidator';

export default {
    mixins: [SmartInputMixin],
    components: {
        FilterListPickInput
    },
    data: () => ({
        nationalityFullList: [],
        filterText: ''
    }),
    computed: {
        nationalityOptions() {
            const full = this.nationalityFullList;
            return full.filter(el => {
                const filter = this.filterText.toLowerCase()

                const criteria = str => {
                    const lc = str.toLowerCase();
                    return lc.indexOf(filter) === 0
                }

                return criteria(el.value) || criteria(el.label);
            })
        }
    },
    async created() {
        this.nationalityFullList = await av.fetchCountryList()
    }
}
</script>
