<template lang="pug">
    .filter-list.pick.input( :class="{toggled: isToggled}" )
        text-input( :value="filter" @input="filterChanged"  v-bind="attrs" v-on="listeners")
        dropdown-options-list.collapsible-list( :value="value" @input="selected" :options="options" )
</template>
<style lang="sass" scoped>

.collapsible-list
    position: absolute
    top: 70px
</style>

<script>

import InputMixin from './Input.mixin'
import ToggleMixin from './Toggle.mixin'
import TextInput from './TextInput'
import DropdownOptionsList from './DropdownOptionsList'

export default {
    mixins: [InputMixin, ToggleMixin],
    components: {
        TextInput,
        DropdownOptionsList
    },
    props: {
        options: Array,
        filter: String
    },
    computed: {
        attrs() {
            const {
                options,
                filter,
                value,
                ...attrs
            } = {...this.$attrs, ...this.$props};

            return attrs;
        }
    },
    methods: {
        selected(data) {
            this.$emit('input', data);
            this.filterChanged(data.label)
            
            this.$nextTick(()=>this.isToggled = false);            
        },
        filterChanged(filterText) {
            this.$emit('update:filter', filterText);
        }
    },
    watch: {
        filter() {
            
            if (this.filter && this.filter.length > 0)
                this.isToggled = true
            else 
                this.isToggled = false
        }
    }
}
</script>
