<template lang="pug">
    list-pick-input( v-bind="attrs" :options="options" v-model="fuzziness" )
</template>
<script>
import ListPickInput from '@/components/inputs/ListPickInput'
import SmartInputMixin from './SmartInput.mixin'
export default {
    mixins: [SmartInputMixin],
    components: {
        ListPickInput
    },
    computed: {
        fuzziness: {
            get() {
                return this.options.find(el=>el.value === this.value)
            },
            set(lvl) {
                this.emitValue(lvl.value)
            }
        },
        options() {
            const options = [
                { label: '100% Broad Match', value: 0 }
            ];
            const interpolation = []
            for (let i = 9 ; i > 0 ; i--) {
                const displayValue = 10*i
                const percentage = `${displayValue}%`
                const inverseValue = (100 - displayValue)*1000/(100*1000) // dealing with javascript imprecision with float points
                options.push({
                    label: percentage,
                    value: inverseValue
                })
            }
            options.push({ label: 'Exact Match', value: 1 });
            return options;
        }
    }
}
</script>
